const columns = [
  {
    title: 'ID',
    dataIndex: 'id',
    fixed: 'left',
  },
  {
    title: '商户名称',
    dataIndex: 'companyName'
  },  
  {
    title: '企业/学校名称',
    dataIndex: 'schoolName'
  },  
  {
    title: '电话/短信',
    dataIndex: 'phone'
  },  
  {
    title: 'logo',
    dataIndex: 'logo',
    render: (text) => (<img src={text} className="m-company-img" alt={'图片'}></img>)
  },  
  {
    title: '百度审核',
    dataIndex: 'checkStatus',
    render: (text) => {
      let hook = {
        0: '已通过',
        1: '审核中'
      }
      return (<span className="m-company-check-status">{hook[text]}</span>)
    }
  }, 
  {
    title: '百度审核时间',
    dataIndex: 'checkTime'
  },
  {
    title: '添加/更新时间',
    dataIndex: 'addTime',
    render: (text, record) => {
      return (<div>{`${text}  ${record.updateTime}`}</div>)
    }
  },
  {
    title: '运营人员',
    dataIndex: 'operators'
  },  
  {
    title: '发布状态',
    dataIndex: 'releaseStatus',
    render: (text) => {
      let hook = {
        0: '下架',
        1: '上架'
      }
      return (<span className="m-company-check-status">{hook[text]}</span>)
    }    
  }, 
]

export { columns }